.navbar {
  background-color: var(--theme-nav-background-color);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
}

.navbar__container {
  display: flex;
  align-items: center;
  width: 100%;
  height: inherit;
  padding: 0px 20px;
  flex: 1;
}

.navbar__container__logo {
  color: var(--theme-nav-main-color);
}

.navbar__container__menu {
  display: flex;
  list-style: none;
  text-align: center;
}

.navbar__container__menu__item {
  line-height: 40px;
  margin-right: 1.5rem;
  font-size: 1.6rem;
}

.navbar__container__menu__item::after {
  content: " ";
  display: block;
  height: 3px;
  width: 0px;
  background: transparent;
  transition: all 0.5s ease;
}

.navbar__container__menu__item:hover::after {
  background-color: var(--theme-nav-main-color);
  width: 100%;
}

.navbar__container__menu__item__links {
  color: var(--theme-nav-main-color);
  text-decoration: none;
  padding: 0.5rem;
}

.nav_Icon {
  display: none;
}

@media only screen and (max-width: 768px) {
  .navbar__container__menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    position: absolute;
    top: 200px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  .active {
    background-color: black;
    left: 0px;
    opacity: 0.8;
  }
  .nav_Icon {
    display: block;
    color: var(--theme-nav-main-color);
    padding-right: 20px;
  }
  .navbar__container__menu__item {
    border-bottom: 1px solid #282828;
    margin: 0px;
    padding: 20px 0px;
  }
}
